"use client"

import { useState } from "react"
import Navigation from "@/components/navigation"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

export default function GeneratorPage() {
  const [generationType, setGenerationType] = useState<"creative" | "ideas">("creative")
  const [prompt, setPrompt] = useState("")
  const [result, setResult] = useState<string | null>(null)
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState<string | null>(null)

  const handleGenerate = async () => {
    if (!prompt.trim()) {
      setError("Veuillez entrer un prompt")
      return
    }

    setLoading(true)
    setError(null)
    setResult(null)

    try {
      const response = await fetch("/api/generator", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          prompt: prompt,
          type: generationType,
        }),
      })

      if (!response.ok) {
        throw new Error("Erreur lors de la génération")
      }

      const data = await response.json()
      setResult(data.content)
    } catch (err) {
      console.error("Erreur:", err)
      setError("Impossible de générer du contenu. Vérifiez votre clé API OpenAI.")
    } finally {
      setLoading(false)
    }
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && e.ctrlKey) {
      handleGenerate()
    }
  }

  return (
    <main className="min-h-screen bg-gradient-to-b from-slate-950 to-slate-900">
      <Navigation />
      <section className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12">
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-white mb-2">Générateur de Contenu</h1>
          <p className="text-slate-300">
            Crée des textes avec l'IA et découvre comment les prompts influencent les résultats générés
          </p>
        </div>

        <Card className="bg-slate-800/50 border-purple-500/30 border-2 p-6 mb-6">
          {/* Type de génération */}
          <div className="mb-6">
            <h3 className="font-bold text-white mb-4">Type de génération</h3>
            <div className="flex gap-4">
              <label className="flex items-center gap-2 cursor-pointer">
                <input
                  type="radio"
                  name="type"
                  value="creative"
                  checked={generationType === "creative"}
                  onChange={() => setGenerationType("creative")}
                  className="w-4 h-4 text-purple-600 accent-purple-600"
                />
                <span className="text-white">Texte créatif</span>
              </label>
              <label className="flex items-center gap-2 cursor-pointer">
                <input
                  type="radio"
                  name="type"
                  value="ideas"
                  checked={generationType === "ideas"}
                  onChange={() => setGenerationType("ideas")}
                  className="w-4 h-4 text-purple-600 accent-purple-600"
                />
                <span className="text-white">Idées de projet</span>
              </label>
            </div>
          </div>

          {/* Prompt */}
          <div className="mb-6">
            <h3 className="font-bold text-white mb-4">Prompt</h3>
            <textarea
              placeholder="Exemple: Écris une histoire courte sur un robot qui apprend à cuisiner..."
              value={prompt}
              onChange={(e) => setPrompt(e.target.value)}
              onKeyDown={handleKeyPress}
              className="w-full h-32 bg-slate-700 border border-slate-600 text-white placeholder:text-slate-400 resize-none rounded-lg p-3 focus:outline-none focus:ring-2 focus:ring-purple-500"
            />
            <p className="text-xs text-slate-400 mt-2">
              Astuce: Sois spécifique et détaillé. Appuyez sur Ctrl+Enter pour générer.
            </p>
          </div>

          {/* Bouton Générer */}
          <Button
            onClick={handleGenerate}
            disabled={loading || !prompt.trim()}
            className="w-full bg-purple-600 hover:bg-purple-700 text-white disabled:opacity-50 disabled:cursor-not-allowed h-12 text-lg font-semibold transition-colors"
          >
            {loading ? "Génération en cours..." : "Générer"}
          </Button>

          {/* Erreur */}
          {error && (
            <div className="mt-4 p-3 bg-red-600/20 border border-red-500 rounded-lg">
              <p className="text-red-300 text-sm">{error}</p>
            </div>
          )}

          {/* Résultat */}
          {result && (
            <div className="mt-6">
              <h3 className="font-bold text-white mb-3">Résultat généré :</h3>
              <div className="p-4 bg-slate-700/50 border border-purple-500/30 rounded-lg">
                <p className="text-slate-100 whitespace-pre-wrap leading-relaxed">{result}</p>
              </div>
            </div>
          )}
        </Card>

        {/* Guides */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <Card className="bg-slate-800/50 border-slate-700 p-6">
            <h3 className="font-bold text-white mb-3">Comment rédiger un bon prompt?</h3>
            <ul className="text-slate-300 text-sm space-y-2">
              <li>• Sois spécifique et détaillé dans ta description</li>
              <li>• Fournis un contexte clair et pertinent</li>
              <li>• Donne des exemples concrets si possible</li>
              <li>• Décris le style et le ton que tu souhaites</li>
            </ul>
          </Card>

          <Card className="bg-slate-800/50 border-slate-700 p-6">
            <h3 className="font-bold text-white mb-3">À découvrir</h3>
            <p className="text-slate-300 text-sm mb-3">
              Essaie le même prompt avec des variantes pour voir comment les petits changements affectent complètement
              la sortie de l'IA.
            </p>
            <p className="text-slate-300 text-sm">
              C'est un excellent moyen d'apprendre l'ingénierie de prompts!
            </p>
          </Card>
        </div>
      </section>
    </main>
  )
}